.Produit {
    width: 250px;
    height: 480px;
    margin: 10px;
    padding: 5px;
    overflow: hidden;
    text-align: center;

    .image {

        img {
            width: 100%;
            border: 1px solid #171738;
        }
    }

    .info {
        font-size: 0.8em;
        line-height: 1.2em;

        h3 {
            margin: 10px;
            height: 40px;
            font-weight: 500;
        }
        
        .prix {
            font-weight: 500;
        }
    }
}