.SommairePanier {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    height: 40px;
    right: 0;
    bottom: -40px;
    padding: 5px;
    font-size: 0.9em;
    background-color: rgb(212, 47, 116);

    span:not(:last-of-type) {
        padding: 0 5px;
        border-right: 1px solid white;
    }
}