button {
    background-color: black;
    color: white;
    width: 200px;
    height: 50px;
    font-size: 1.2em;
    border: 2px solid black;
    transition: background-color 0.2s ease, color 0.2s ease;
}

button:hover,
.actif {
    cursor: pointer;
    background-color: white;
    color: black;
}

.rouge{
    background-color: white;
    color: black;
}