.Entete {
    position: sticky;
    top: 0;
    height: 60px;
    margin: 0 auto;
    padding: 20px 50px;
    padding-bottom: 30px;
    font-size: 1.2em;
    color: white;
    background-color: black;
    display: flex;
    align-items: center;
    
    div.logo > a {
        font-family: 'Indie Flower', cursive;
        color: #ee3f79;
    }

    ul {
        margin: 10px 0 0 0;
        padding: 0;
        list-style: none;
        display: flex;
        font-size: 0.8em;

        li > a {
            cursor: pointer;
            color: white;
            transition: color 0.2s ease;
        }

        li:not(:last-of-type) {
            padding-right: 15px;
            border-right: 1px solid white; 
        }

        li:not(:first-of-type) {
            padding-left: 15px;
        }

        &.navPrincipale {
            margin-left: 50px;
            margin-right: 20px;
        }

        &.navUtil {
            margin-left: auto;
        }
    }

    .navPrincipale {
        li > a:hover {
            color: #ee3f79;
        }
    }

    .navUtil {
        li:hover {
            color: #f0c695;
        }
    }
}